import React, {useEffect, useState} from 'react';
import { Steps } from 'antd';
import '../footer/styles/HowDoWeWork.css';
import circleIcon from '../../images/common/Circle.svg';

const { Step } = Steps;

export function HowDoWeWork() {
    let [smallSize, setSmallSize] = useState(window.innerWidth < 1300);

    useEffect(() => {
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    });

    const handleResize = () => {
        if(window.innerWidth < 1300) {
            !smallSize && setSmallSize(true);
        } else {
            smallSize && setSmallSize(false);
        }
    };

    return (
        <div>
          <p className='d-flex justify-content-center how-do-we-work-text'>  </p>
          <div className='d-flex justify-content-center col-11 mx-auto'>
              <Steps current={6} labelPlacement='vertical' direction={smallSize ? 'vertical' : 'horizontal'}>
                  <Step title=" 1" icon={<img src={circleIcon}/>} subTitle=' ' description="        ,            ." />
                  <Step title=" 2" icon={<img src={circleIcon}/>} subTitle=' ' description="                      ." />
                  <Step title=" 3" icon={<img src={circleIcon}/>} subTitle=' ' description="                ." />
                  <Step title=" 4" icon={<img src={circleIcon}/>} subTitle=' ' description="             ." />
                  <Step title=" 5" icon={<img src={circleIcon}/>} subTitle=' ' description="             ." />
              </Steps>
          </div>
        </div>
  );
}
